import React from "react"
import HelperMore from "./_helperMore"
import "./Styles/switch.css"
const BaseText = props => {
  const more = HelperMore.getMoreFromProps(props)
  let _optionPositive = HelperMore.getOptionPositive(more, {
    default: {
      Value: true,
      Text: "YES",
    },
  })
  let _optionNegative = HelperMore.getOptionNegative(more, {
    default: {
      Value: false,
      Text: "NO",
    },
  })
  return (
    <div style={{ display: "flex", alignItems: "center" }}>
      <label
        className="cui-sw cui-sw-green"
        onClick={() => {
          // console.warn("change:",props.value,_optionPositive.Value,_optionNegative.Value)
          props.onChange &&
            props.onChange(
              props.value === _optionPositive.Value
                ? _optionNegative.Value
                : _optionPositive.Value
            )
        }}
      >
        <input
          type="checkbox"
          className="cui-sw-input"
          checked={props.value === _optionPositive.Value ? true : false}
          disabled
        />
        <span
          className="cui-sw-label"
          data-on={_optionPositive.Text}
          data-off={_optionNegative.Text}
        ></span>
        <span className="cui-sw-handle"></span>
      </label>
    </div>
  )
}

export default BaseText
